import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';

export interface Spinner {
  name: string;
  status: boolean;
  data?: string;
}

@Injectable({
  providedIn: 'root',
})
export class SpinnerService {
  private change: Subject<Spinner> = new Subject<Spinner>();

  constructor() {}

  get change$(): Observable<Spinner> {
    return this.change.asObservable();
  }

  show(name: string = 'spinner', msg: string = 'loading'): void {
    this.change.next({ name, status: true, data: msg });
  }

  hidde(name: string = 'spinner'): void {
    this.change.next({ name, status: false });
  }
}
